import {UPSTATE} from './action-types';
import axios from 'axios';

//
const updata=(data)=>({
    type:UPSTATE,
    data:data
})

//
export const upData=(id)=>{
    return dispatch=>{
        //ajax
        axios.get(`https://cnodejs.org/api/v1/topics?tab=${id}`)
            .then((res)=>{

                dispatch(updata(res.data.data));
            })
            .catch((error)=>{

            })
        //action


    }

}


