import React,{Component} from 'react';
import {Link} from 'react-router-dom';

export default class Header extends Component {
    render(){
        return(
            <div className='navLink'>
                <div className="centerMain">
                    <Link to='/home'></Link>
                    <Link to='/book'></Link>
                    <Link to='/about'></Link>
                </div>
            </div>
        )
    }
}


