import React,{Component} from 'react';
import {Link} from 'react-router-dom';
export default class HomeNav extends Component {

    render(){
        return(
            <div className=''>
                <ul>
                    <li><Link to='/home/all'></Link></li>
                    <li><Link to='/home/good'></Link></li>
                    <li><Link to='/home/ask'></Link></li>
                    <li><Link to='/home/share'></Link></li>
                    <li><Link to='/home/job'></Link></li>
                    <li><Link to='/home/dev'></Link></li>
                </ul>

            </div>
        )
    }
}


